import react,{Component,usestate} from "react"


class personaldetails extends Component
{
    constructor(props){
        super(props);
        this.state={
            items:[],
        };
    }
    componentDidMount(){
        fetch("https://api.github.com/users")
        .then((res)=>res.json())
        .then((json)=>
        {
            this.setState({
                items:json,
            });
        });

    }
    render(){
        let {items} =this.state
        return(
            <div className='details'>
                {items.map((x)=>(
                    <ol key={x.id}>
                        {" "}
{x.id})<b>Employe name:</b>{x.login}<br></br>
<img src={x.avathar_url} ></img>
                    </ol>
                ))}

            </div>
        );

    }
}
export default personaldetails;